@import url(https://fonts.googleapis.com/css?family=Ubuntu);
/* Tom
* added Ubuntu to body
*/

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Tom/Libor
* css for the whole application layout
*/

.App {
  font-family: "Ubuntu";
  background: white;
  text-align: center;
  color: black;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(26px + 2vmin);
  color: black;
}

.App-profile {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(26px + 2vmin);
  color: black;
}

.App-navigator {
  background-color: white;
  color: black;
  -webkit-align-items: left;
          align-items: left;
  text-align: justify;
}

.App-logForm {
  color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.buttons{
  display: -webkit-flex;
  display: flex;
  padding-left: calc(30px + 2vmin);
}

.errorText{
  display: -webkit-flex;
  display: flex;
  color: red;
  padding-left: calc(30px + 2vmin);
}

.App-footer {
  background-color: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(1px + 2vmin);
  color: black;
}

.App-about{
  margin-left: 30px;
  margin-right: 30px;
  color:black;
}

.App-soon{
  color:black;
  font-size: 30px;
  text-align: center;
}

.App-reminder{
  padding-top: 20px;
  padding-bottom: 10px;
}

.form{
  margin-top: 5px;
  -webkit-margin-after: 0%;
          margin-block-end: 0%;
}

.form-desc{
  -webkit-margin-after: 2%;
          margin-block-end: 2%;
  width: 25rem;
}

.form-button{
  -webkit-margin-after: 5%;
          margin-block-end: 5%;
}



@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Tom
* css for Profile feature
*/
.Profile-info {
  text-align: left;
  margin-top: 20px;
  margin-left: 30px;
}
.Profile-heading {
  font-size: 24px;
  font-weight: 1000;
}
.Profile-resetPassword {
  text-align: left;
  margin-left: 30px;
}
.Profile-change {
  text-align: right;
  margin-right: 30px;
}
.Profile-charakters {
  font-weight: bold;
}
.Profile-errorText{
  display: -webkit-flex;
  display: flex;
  color: red;
  padding-left: calc(30px + 2vmin);
}

/* Tom
* css for Admin.js
*/

.Admin-log{
  text-align: left;
}

.Admin-title{
  font-size: 20px;
